import React from "react";
import styled from "styled-components";

const StyledVideo = styled.video`
    height: 500px;
    width: 100%;
`;
const Video = ({userVideo}) => {
  return (
      <StyledVideo
          className="video-border"

          ref={userVideo}
          autoPlay
          playsInline
          style={{position: 'relative'}}
      >

      </StyledVideo>
  );
};

export default Video;